import React,{useState, useEffect,useContext} from 'react'
import clsx from 'clsx';
import { PageTitle } from '../../layout-components';

import { Row, Col, Card, TabContent, TabPane, Nav, NavItem } from 'reactstrap';
import { NavLink as NavLinkStrap } from 'reactstrap';
import Slider from 'react-slick';

import StateInsights from '../../custom-components/StateInsights';
import StateInsightMap from '../../custom-components/StateInsightMap';
import {InsightsDoubleClickContext} from 'contexts/InsightsDoubleClickContext';

export default function InsightsDoubleClick() {
    const {setSentimentProfile} = useContext(InsightsDoubleClickContext)

    useEffect(()=>{

        fetch('/api/insightsDoubleClick').then(res=>{
            if(res.ok){
                return res.json()
            }
        }).then(data => {
            console.log(data)
            setSentimentProfile(data)
        })
    },[]);

    return (
        <>
            <PageTitle
                titleHeading="Insight Double-Click"
                titleDescription="Exploring the breakdown of each key research insights"
            />
            <StateInsights />
            <StateInsightMap />
        </>
    )
}
